import React, { useState } from "react";
import {
  Container,
  Row,
  Col,
  Button,
  Image,
  ProgressBar,
} from "react-bootstrap";
import ImageIcon from "@material-ui/icons/Image";
import * as mobilenet from "@tensorflow-models/mobilenet";
import "./App.css";

function App() {
  const [img, setImg] = useState("None");
  const [res, setRes] = useState(0);
  const [show, setShow] = useState(false);

  const imageHandler = (e) => {
    setShow(false);
    const reader = new FileReader();
    reader.onload = () => {
      if (reader.readyState === 2) {
        setImg(reader.result);
      }
    };
    reader.readAsDataURL(e.target.files[0]);
  };



  const classifyImg = async () => {
    // Initialize the Image Classifier method with MobileNet

    const net = await mobilenet.load();
    const img = document.getElementById("img");
    const result = await net.classify(img);
    setRes(result);
    setShow(true);
  };

  return (
    <div className="App">
      <Container style={{ minHeight: "90vh", minWidth: "96vw" }}>
        <Row className="center">
          <h6>Upload an image...</h6>
        </Row>
        <Row className="center">
          {img === "None" ? (
            <ImageIcon
              style={{ fontSize: "9em" }}
              style={{ width: "240px", height: "240px" }}
            />
          ) : (
              <Image
                src={img}
                id="img"
                className="img"
                style={{ fontSize: "1em" }}
                style={{ width: "400px", height: "300px" }}
              />
            )}
        </Row>

        <Row className="center">
          <input
            variant="primary"
            type="file"
            accept="image/*"
            name="image-upload"
            id="input"
            onChange={imageHandler}
          />
        </Row>

        <Row className="center">
          <Button variant="primary" onClick={classifyImg}>
            Classify
          </Button>
        </Row>

        {show && (
          <ul>
            {res.map(({ className, probability }) => (
              <div style={{ width: "50%", padding: "10px", margin: "auto" }}>
                <ProgressBar
                  key={className}
                  variant="success"
                  now={(probability * 100).toFixed(2)}
                  label={`${(probability * 100).toFixed(2)}%`}

                />
                <h6>{`${className}`}</h6>
              </div>
            ))}
          </ul>
        )}
      </Container>
    </div>
  );
}

export default App;
